/* frontend/src/components/Nav.css */
.nav {
  background-color: #0077b6; /* Azul intenso */
  padding: 15px 30px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.nav-logo {
  color: #ffffff; /* Azul muy claro */
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  margin: 0;
  transition: color 0.3s;
}

.nav-logo:hover {
  color: rgb(148, 237, 255); /* Azul vibrante */
}

.nav-menu-button {
  display: none; /* Oculto en escritorio */
  background: none;
  border: none;
  color: #ffffff; /* Azul muy claro */
  font-size: 1.8rem;
  cursor: pointer;
}

.nav-menu {
  display: flex;
  gap: 25px;
  align-items: center;
}

.nav-menu-item {
  background: none;
  border: none;
  color: #ffffff; /* Azul claro */
  font-size: 1.1rem;
  cursor: pointer;
  transition: color 0.3s;
}

.nav-menu-item:hover {
  color: rgb(148, 237, 255); /* Azul vibrante */
}

.nav-user {
  color: #ffffff; /* Azul muy claro */
}

/* Responsive */
@media (max-width: 768px) {
  .nav-menu-button {
    display: block;
  }

  .nav-menu {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    background-color: #0077b6; /* Azul intenso */
    padding: 20px;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
  }

  .nav-menu.open {
    display: flex;
  }

  .nav-menu-item {
    font-size: 1.2rem;
    padding: 10px 0;
  }
}