/* frontend/src/pages/PedirTurno.css */
.pedir-turno-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .pedir-turno-container {
    flex: 1;
    background-color: #caf0f8; /* Azul muy claro */
    padding: 40px 20px;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .pedir-turno-title {
    font-size: 2rem;
    color: #03045e; /* Azul intenso */
    text-align: center;
    margin-bottom: 20px;
  }
  
  .pedir-turno-error {
    color: #d32f2f;
    text-align: center;
    margin-bottom: 15px;
    font-size: 1rem;
  }
  
  .pedir-turno-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .pedir-turno-field {
    display: flex;
    flex-direction: column;
  }
  
  .pedir-turno-label {
    font-size: 1.1rem;
    color: #03045e; /* Azul intenso */
    margin-bottom: 5px;
  }
  
  .pedir-turno-input {
    padding: 12px;
    font-size: 1rem;
    border-radius: 8px;
    border: 1px solid #90e0ef; /* Azul claro */
    background-color: #fafafa;
    outline: none;
    transition: border-color 0.3s, box-shadow 0.3s;
  }
  
  .pedir-turno-input:focus {
    border-color: #00b4d8; /* Azul vibrante */
    box-shadow: 0 0 5px rgba(0, 180, 216, 0.5);
  }
  
  .pedir-turno-servicios {
    font-size: 0.9rem;
    color: #0077b6; /* Azul medio */
    margin: 0;
    padding: 10px;
    background-color: white;
    border-radius: 8px;
  }
  
  .pedir-turno-textarea {
    padding: 12px;
    font-size: 1rem;
    border-radius: 8px;
    border: 1px solid #90e0ef; /* Azul claro */
    background-color: #fafafa;
    outline: none;
    resize: vertical;
    min-height: 100px;
    transition: border-color 0.3s, box-shadow 0.3s;
  }
  
  .pedir-turno-textarea:focus {
    border-color: #00b4d8; /* Azul vibrante */
    box-shadow: 0 0 5px rgba(0, 180, 216, 0.5);
  }
  
  .pedir-turno-button {
    padding: 12px;
    font-size: 1.2rem;
    background-color: #0077b6; /* Azul medio */
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  
  .pedir-turno-button:hover:not(:disabled) {
    background-color: #00b4d8; /* Azul vibrante */
    transform: scale(1.02);
  }
  
  .pedir-turno-button:disabled {
    background-color: #90e0ef; /* Azul claro */
    cursor: not-allowed;
  }