/* frontend/src/pages/Home.css */
.home-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .home-container {
    flex: 1;
    background-color: #caf0f8; /* Azul muy claro */
    padding: 40px 20px;
  }
  
  .home-header {
    text-align: center;
    padding: 50px 20px;
    background-color: #90e0ef; /* Azul claro */
    border-radius: 15px;
    margin-bottom: 40px;
  }
  
  .home-title {
    font-size: 2.5rem;
    color: #03045e; /* Azul intenso */
    margin: 0 0 10px;
  }
  
  .home-subtitle {
    font-size: 1.2rem;
    color: #0077b6; /* Azul medio */
    margin: 0 0 20px;
  }
  
  .home-cta-button {
    padding: 12px 24px;
    font-size: 1.2rem;
    background-color: #0077b6; /* Azul medio */
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  
  .home-cta-button:hover {
    background-color: #00b4d8; /* Azul vibrante */
    transform: scale(1.05);
  }
  
  .home-section {
    max-width: 1200px;
    margin: 0 auto 40px;
    text-align: center;
  }
  
  .home-section-title {
    font-size: 2rem;
    color: #03045e; /* Azul intenso */
    margin-bottom: 20px;
  }
  
  .home-text {
    font-size: 1rem;
    color: #03045e; /* Azul intenso */
    max-width: 800px;
    margin: 0 auto;
  }
  
  .home-cards-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  .home-card {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(3, 4, 94, 0.1);
    width: 100%;
    max-width: 300px;
    transition: transform 0.3s;
  }
  
  .home-card:hover {
    transform: translateY(-5px);
  }
  
  .home-card-title {
    font-size: 1.5rem;
    color: #0077b6; /* Azul medio */
    margin: 0 0 10px;
  }
  
  .home-card-text {
    font-size: 0.9rem;
    color: #03045e; /* Azul intenso */
  }
  
  .home-steps-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  
  .home-step {
    display: flex;
    align-items: center;
    gap: 15px;
    max-width: 300px;
  }
  
  .home-step-number {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: #0077b6; /* Azul medio */
    color: white;
    border-radius: 50%;
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  .home-step-text {
    font-size: 1rem;
    color: #03045e; /* Azul intenso */
    margin: 0;
  }